import { SET_LOADER } from '../actions/loaderAction'

const initState = {
  active: false
}

const loaderReducer = (state = initState, action) => {
  if (action.type === SET_LOADER) {
    return {
      ...state,
      active: action.payload.active
      // active: !state.active,
    }
  }
  return state
}

export default loaderReducer
