/**
 * Update products for theme
 * Stripe products dont have all properties as theme
 * Add blank properties to align with theme's layout bindings
 * @param {*} products
 */
export const updateProductsForTheme = products => {
  products = products.map(product => {
    // TODO: dummy variation added to not break theme calculations
    // to be adjusted later
    product.variation = [{
      color: 'white',
      image: '/assets/img/product/fashion/1.jpg',
      size: [
        {
          name: 'x',
          stock: 3
        },
        {
          name: 'm',
          stock: 2
        },
        {
          name: 'xl',
          stock: 5
        }
      ]
    }]
    const productImages = product.images
    delete product.images
    product.image = productImages
    if (!product.image.length) {
      // if no image then add a dummy image page
      // to align easily with already existing them structure
      product.image.push('/assets/img/product/comingSoon.jpg')
    }
    return product
  })
  return products
}

// get products
export const getProducts = (products, category, type, limit) => {
  const finalProducts = category
    ? products.filter(
      product => product.category.filter(single => single === category)[0]
    )
    : products

  if (type && type === 'new') {
    const newProducts = finalProducts.filter(single => single.new)
    return newProducts.slice(0, limit || newProducts.length)
  }
  if (type && type === 'bestSeller') {
    return finalProducts
      .sort((a, b) => {
        return b.saleCount - a.saleCount
      })
      .slice(0, limit || finalProducts.length)
  }
  if (type && type === 'saleItems') {
    const saleItems = finalProducts.filter(
      single => single.discount && single.discount > 0
    )
    return saleItems.slice(0, limit || saleItems.length)
  }
  return finalProducts.slice(0, limit || finalProducts.length)
}

// check global currency setting and return price
export const getProductPrice = (product, currency = 'usd') => {
  const selectedPriceObj = product.prices.find(priceInfo => {
    return priceInfo.currency === currency
  })
  return selectedPriceObj ? selectedPriceObj.unit_amount / 100 : 50
}

// get product discount price
export const getDiscountPrice = (price, discount) => {
  return discount && discount > 0 ? price - price * (discount / 100) : null
}

// get product cart quantity
export const getProductCartQuantity = (cartItems, product, color, size) => {
  const productInCart = cartItems.filter(
    single =>
      single.id === product.id &&
      (single.selectedProductColor
        ? single.selectedProductColor === color
        : true) &&
      (single.selectedProductSize ? single.selectedProductSize === size : true)
  )[0]
  if (cartItems.length >= 1 && productInCart) {
    if (product.variation) {
      return cartItems.filter(
        single =>
          single.id === product.id &&
          single.selectedProductColor === color &&
          single.selectedProductSize === size
      )[0].quantity
    } else {
      // TODO: Workaround for WP only product
      // return cartItems.filter(single => product.id === single.id)[0].quantity;
      return cartItems.filter(single => product.id === single.id) ? cartItems.filter(single => product.id === single.id)[0].quantity : 1
    }
  } else {
    return 0
  }
}

// get products based on category
export const getSortedProducts = (products, sortType, sortValue) => {
  if (products && sortType && sortValue) {
    if (sortType === 'category') {
      return products.filter(
        product => product.category.filter(single => single === sortValue)[0]
      )
    }
    if (sortType === 'tag') {
      return products.filter(
        product => product.tag.filter(single => single === sortValue)[0]
      )
    }
    if (sortType === 'color') {
      return products.filter(
        product =>
          product.variation &&
          product.variation.filter(single => single.color === sortValue)[0]
      )
    }
    if (sortType === 'size') {
      return products.filter(
        product =>
          product.variation &&
          product.variation.filter(
            single => single.size.filter(single => single.name === sortValue)[0]
          )[0]
      )
    }
    if (sortType === 'filterSort') {
      const sortProducts = [...products]
      if (sortValue === 'default') {
        return sortProducts
      }
      if (sortValue === 'priceHighToLow') {
        return sortProducts.sort((a, b) => {
          return b.price - a.price
        })
      }
      if (sortValue === 'priceLowToHigh') {
        return sortProducts.sort((a, b) => {
          return a.price - b.price
        })
      }
    }
  }
  return products
}

// get individual element
const getIndividualItemArray = array => {
  const individualItemArray = array.filter(function (v, i, self) {
    return i === self.indexOf(v)
  })
  return individualItemArray
}

// get individual categories
export const getIndividualCategories = products => {
  const productCategories = []
  products &&
    products.map(product => {
      return (
        product.category &&
        product.category.map(single => {
          return productCategories.push(single)
        })
      )
    })
  const individualProductCategories = getIndividualItemArray(productCategories)
  return individualProductCategories
}

